@import url('https://fonts.googleapis.com/css2?family=Dosis:wght@200&family=Nunito:wght@200&display=swap');

body {
  background-color: #222222;
  color: aliceblue;
  font-family: "Nunito";
}
::-webkit-scrollbar {
  display: none;
}
.border {
  border: solid 1px red;
}
.title {
  font-size: 5vh;
  align-self: center;
}
.subrayado {
  border-bottom: solid 0.3vh aliceblue;
}
.App {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.education, .experience, .home, .contact {
  display: flex;
  flex-direction: column;
  width: 90vw;
  margin: 1vh 1vw 0 1vw;
  padding: 0.2vh 1vh;
}