.navbar {
    background-color: #4b4b4b;
    height: 10vh;
    width: 90vw;
    margin: 2vh 1vw 0 1vw;
    padding: 0.2vh 1vh;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.navbar-icon {
    height: 8vh;
}
.menu-icon {
    height: 8vh;
    width: 8vh;
    background-color: inherit;
    border: 1px solid aliceblue;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 6vh;
    color: inherit;
    text-decoration: none;
    cursor: pointer;
}

.nav-menu {
    width: 94vw;
    margin: 1vh 1vw 2vh 1vw;
    list-style: none;
    text-align: center;
    background-color: #4b4b4b;
    position: absolute;
    top: 12.3vh;
    left: -110%;
    transition: all 0.5s ease;
    display: flex;
    flex-direction: column;
}
.nav-menu.active {
    top: 12.3vh;
    left: 2.2vw;
    transition: all 0.5s ease;
    z-index: 1;
}
.nav-links {
    color: inherit;
    text-decoration: none;
    font-size: 10vw;
    margin-top: 3vh;
}
.last {
    margin-bottom: 3vh;
}