.contact {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
}
.icons {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.fa {
    padding: 5vw;
    font-size: 5vh;
    height: 5%;
    width: 40vw ;
    text-align: center;
    text-decoration: none;
    margin: 1vh 1vw;
}
.fa-whatsapp {
    background: #075e54;
    color: white;
}
.fa-github {
    color: white;
    background: black;
}
.fa-linkedin {
    background: #007bb5;
    color: white;
}
button.fa-envelope {
    color: white;
    background: black;
    border: none;
    width: 97%;
    cursor: pointer;
}
button:focus {
    outline:none;
}